        {codPost === "compostagem3" && (
          <>
            <div className="content">
              <h1>
                O Que No Colocar em uma Composteira: Cuidados Essenciais para
                uma Compostagem Eficiente
              </h1>
              <p>
                A compostagem  uma prtica extremamente benfica para o meio
                ambiente, pois contribui para a reduo de resduos, melhora a
                qualidade do solo e oferece uma alternativa natural aos
                fertilizantes qumicos. No entanto, para garantir que o processo
                de decomposio ocorra de maneira adequada e que o composto
                gerado seja de qualidade,  fundamental saber o que no deve ser
                colocado em uma composteira. Alguns materiais podem dificultar a
                decomposio, atrair pragas ou at contaminar o composto.
              </p>
              <p>
                Neste artigo, vamos apresentar os principais itens que devem ser
                evitados na compostagem domstica.
              </p>
              <h2>Materiais a Evitar na Compostagem</h2>
              <h3>1. Carnes, Peixes e Ossos</h3>
              <p>
                Carnes, peixes, ossos e outros produtos de origem animal no
                devem ser compostados. Alm de demorarem muito para se decompor,
                esses materiais atraem roedores, insetos e outros animais
                indesejados. O processo de decomposio tambm pode gerar odores
                fortes e desagradveis, comprometendo a qualidade do composto.
              </p>
              <h3>2. Laticnios (Leite, Queijo, Manteiga, etc.)</h3>
              <p>
                Laticnios, como leite, queijo e manteiga, tambm so imprprios
                para compostagem. Eles possuem alto teor de gordura e protenas,
                o que pode dificultar a decomposio. Assim como as carnes,
                esses produtos atraem pragas e podem gerar odores desagradveis.
                Alm disso, eles podem criar um ambiente propcio ao crescimento
                de bactrias indesejveis.
              </p>
              <h3>3. Alimentos Gordurosos ou Fritos</h3>
              <p>
                leos e alimentos fritos no so compostveis. A gordura
                presente nesses alimentos impede a decomposio adequada dos
                resduos, podendo criar uma camada impermevel que dificulta a
                circulao de ar. Isso pode levar  criao de um ambiente
                anaerbico, causando o desenvolvimento de odores ftidos e
                prejudicando o processo de compostagem.
              </p>
              <h3>4. Resduos de Plantas Doentes ou Infestadas</h3>
              <p>
                 importante evitar compostar plantas doentes ou infestadas por
                pragas. Embora o calor no interior da compostagem possa ajudar a
                destruir algumas pragas e patgenos, ele pode no ser suficiente
                para erradicar completamente doenas e parasitas. Compostar
                esses materiais pode, portanto, resultar na propagao de
                doenas para outras plantas no jardim ou na horta.
              </p>
              <h3>5. Plsticos, Vidro e Metais</h3>
              <p>
                Plsticos, vidro e metais no se decompem e, portanto, no
                devem ser colocados em uma composteira. Esses materiais no so
                biodegradveis e podem contaminar o composto, prejudicando a
                qualidade do fertilizante natural. Alm disso, muitos plsticos
                e metais contm substncias qumicas que podem afetar
                negativamente o solo.
              </p>
              <h3>6. Produtos Qumicos e Txicos</h3>
              <p>
                Evite adicionar produtos qumicos, como pesticidas, herbicidas,
                fertilizantes sintticos e outros produtos txicos ao composto.
                Esses itens podem matar microorganismos benficos e contaminar o
                solo com substncias nocivas. A compostagem deve ser um processo
                natural e orgnico, sem a interferncia de produtos qumicos que
                comprometem a sade do solo e das plantas.
              </p>
              <h3>7. Cabelos e Plos de Animais (em Grandes Quantidades)</h3>
              <p>
                Embora pequenas quantidades de cabelos e plos de animais possam
                ser compostadas, quando adicionados em grandes quantidades,
                esses materiais podem dificultar a decomposio. Alm disso,
                eles podem atrair pragas, como roedores. Caso deseje compostar
                cabelos ou plos, certifique-se de mistur-los bem com outros
                materiais, para evitar acmulo excessivo.
              </p>
              <h3>8. Cascas de Frutas com Agrotxicos</h3>
              <p>
                Cascas de frutas que foram tratadas com pesticidas ou outros
                produtos qumicos devem ser evitadas, pois podem conter resduos
                txicos que afetam a qualidade do composto. O ideal  optar por
                frutas orgnicas ou, caso no seja possvel, lavar bem as cascas
                antes de compost-las.
              </p>
              <h3>9. Carvo e Cinzas de Madeira Tratada</h3>
              <p>
                Cinzas de carvo ou de madeira tratada no devem ser
                compostadas, pois podem conter substncias qumicas e metais
                pesados. As cinzas tambm podem alterar o pH do composto,
                tornando-o excessivamente alcalino, o que dificulta a
                decomposio e a utilizao do composto para fertilizao de
                plantas.
              </p>
              <h3>10. Papel Brilhante, Laminado ou Com Revestimentos</h3>
              <p>
                Papis brilhantes, papis laminados e aqueles com revestimentos
                plsticos no so compostveis. Embora o papel em si seja
                biodegradvel, esses tipos de papel contm substncias
                sintticas que no se decompem adequadamente e podem contaminar
                o composto. Opte por papis simples e no revestidos, como
                jornais, papelo ou papel de escritrio.
              </p>
              <p>
                <span style={{ background: "#00681f", color: "#fff" }}>
                  Lembre-se:
                </span>{" "}
                a compostagem  uma prtica que depende do equilbrio entre
                diferentes tipos de resduos, e saber o que no colocar  to
                importante quanto escolher os materiais certos. Com cuidados e
                ateno, sua compostagem domstica ser um sucesso!
              </p>
            </div>
          </>
        )}

